import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, fetchEncryptStream, ObjectKeys, genNumId, parseFloatToDecimal, qryStore } from "../../../stores/Utils";

import {  TextInput_Cmpt, SvgIcon_Cmpt, AccountLabelDetail_Cmpt } from "../../../components";


import "./styles.css"
import { CloseModal, OpenModal } from "../../../components/DialogHRM";

import { SelectDropAccount_Cmpt } from "../../ComprobanteParametrizados";

const Svg = SvgIcon_Cmpt();

const TextInput = TextInput_Cmpt();

const DropInput = SelectDropAccount_Cmpt();

const AccountLabelDetail = AccountLabelDetail_Cmpt();







const AddExtraExpenses = ({data, collectionName, confirm}) => {
	const [obs, set_obs] = useState(0);

	let accountD = getStore("account_ids")?.[getStore("extraAccountPicker")?.account];
	let subaccountD = getStore("sub_account_ids")?.[getStore("extraAccountPicker")?.subAccount];


	const updFrmExtraAccounts = (f, v, k) => {
		let frm2 = getStore("extraAccountPicker") || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore("extraAccountPicker", frm2);
		set_obs(gen16CodeId())
	};

	const addExtraAccount2List = () => {
		let frm2 = {...getStore("extraAccountPicker")} || {};
		let extraAccountListOnCmp = {...getStore(collectionName)} || {};
		let id = frm2["account"] + "_" + frm2["subAccount"];
		frm2["type"] = "debit";
		extraAccountListOnCmp[id] = frm2;
		updStore(collectionName, extraAccountListOnCmp);
		
		updStore("extraAccountPicker", null);
		set_obs(gen16CodeId());
		confirm && confirm();
		CloseModal({id:data.modalID})
	}

	const handlePick = (acc,subacc) => {
		let nn= {
			"account":acc,
			"subAccount":subacc
		}
		updStore("extraAccountPicker", nn);
		set_obs(gen16CodeId())
	}


	
	
	const clear = () => {
		
		updStore("extraAccountPicker", null);
		set_obs(gen16CodeId())
	};

	return (
		<div style={{
			maxHeight: "80vh",
			width: "560px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Agregar gastos extras
				</p>
				<div className="flexSpace"/>
				
			</div>
		{getStore("extraAccountPicker")?.account ? 
			<div className="_dsplFlx " style={{margin: "18px 0"}}>
				<div className=" acc_dtls">
					<div className="_dsplFlx"   onClick={clear}>
						<div className="code" style={{color: "#646cff"}}>
							{accountD?.upc_barcode} 
						</div>
						<div className="name">
							{accountD?.name}
						</div>
					</div>
					{subaccountD ?
						<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
							<div className="code" >
								{subaccountD?.subAccount} 
							</div>
							<div className="name">
								{subaccountD?. subAccountName}
							</div>
						</div>
						:null}
					<div className="flexSpace"/>
				</div>
			</div> :
			<DropInput
				width={420}
				handleClick={handlePick}
			/>
			}
			<div className="" style={{maxWidth: 130 , marginLeft: 7}}>
					<TextInput  
						init={getStore("extraAccountPicker")?.["amount"]} 
						label={"Importe"} 
						width={120} 
						type={"number"}
						updChanges={(e)=>updFrmExtraAccounts("amount",e)}
					/>
			</div>


			
			<div className="card">
				<button onClick={addExtraAccount2List}>
					Agregar
				</button>
			</div>
			</div>
	)
}


const ExtraExpenses = ({collectionName}) => {
	const [obs, set_obs] = useState(0);



	const updObs = () => {
		set_obs(gen16CodeId())
	}
	
	
	const rmvExtraAccount2List = (id) => {
		let extraAccountListOnCmp = getStore(collectionName) || {};
		delete extraAccountListOnCmp[id];
		updStore(collectionName, extraAccountListOnCmp);
		set_obs(gen16CodeId())
	}



	const openAddExtra =  () => {
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "80vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<AddExtraExpenses collectionName={collectionName} confirm={updObs} />
		
		OpenModal(dataModal);
	}


	return (
		
		<div className="boxAddAcc" style={{margin: "18px 0"}}>

			<div className="_dsplFlx"  >
				<p className="group_title addBtn purple" onClick={openAddExtra} >
					Agregar gastos
				</p>
			</div>
			
			<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
				<div className="tr_bar tr_bar_25">
					Cuenta
				</div>
				<div className="tr_bar tr_bar_25">
					Sub Cuenta
				</div>
				<div className="tr_bar tr_bar_25">
				
				</div>
				<div className="tr_bar tr_bar_25">
					Monto
				</div>
			</div>
			{getStore(collectionName) && ObjectKeys(getStore(collectionName))?.map((element, indE)=>
				<ListingExtraAccounts key={element} idKey={element} data={getStore(collectionName)[element]} deleteItem={rmvExtraAccount2List}/>
			)}
		</div>

	)
}




const ListingExtraAccounts  = ({ data, deleteItem, idKey }) => {
	let accountD = getStore("account_ids")?.[data?.account];
	let subaccountD = getStore("sub_account_ids")?.[data?.subAccount];

	const editSearchAccount = () => {
		deleteItem(idKey)
	}
	
	return (
		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar tr_bar_25">
				<AccountLabelDetail 
					data={accountD}
					labelField={"code"}
					infoField={"name"}
				/>
			</div>
			<div className="tr_bar tr_bar_25">
				<AccountLabelDetail 
					data={subaccountD}
					labelField={"subAccount"}
					infoField={"subAccountName"}
				/>
			</div>
			<div className="tr_bar tr_bar_25">
				
			</div>
			<div className="tr_bar tr_bar_25">
				{data?.type === "debit" ? parseFloatToDecimal(data?.amount,2) : "0.00" }
			</div>
			<div className="flexSpace"/>
			<div className="icon" onClick={editSearchAccount}>
				<Svg name={"outline_delete"} color={"#c62828"}/>
			</div>

		</div>
	)

}

export default ExtraExpenses;